.weatherContainer{
  margin: 0px 3%;
  padding: 10px;
  height: 68vh;

  background-repeat:no-repeat;
  background-size: cover;
  background-position: center;
 display: flex;
 flex-direction: column;
 justify-content: flex-start;
 align-items: center;
 border-radius: 30px;
}
.content{
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.allText{
  display: flex;
  flex-direction: column;
  color: white;
}
.parameters{
  padding: 8px;
  text-align: center;
  color: whitesmoke;
  background-color: rgba(0,0,0.3, 0.4);
  border-radius: 20px;
  display: inline;
  width:250px;
  position: absolute;
  margin-top: 12rem;
  /* bottom: 20px; */
}
body{
  background-color: rgb(133, 131, 131);
  color: white;
}

